header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding:2rem 7%;
    border-bottom: .1rem solid rgba(0,0,0,.1);
    position: relative;
    z-index: 1000;
  }
  
  header .logo{
    font-size: 2.5rem;
    color:var(--violet);
    font-weight: bold;
  }
  
  header .logo span{
    color:var(--pink);
  }
  
  header .navbar a{
    margin-left: 2rem;
    font-size: 2rem;
    color:#333;
  }
  
  header .navbar a:hover{
    color:var(--pink);
  }
  .nav-close, .nav-toggle{
    display: none;
  }
  #menu{
    font-size: 2.5rem;
    padding:.5rem 1rem;
    border-radius: .3rem;
    border:.1rem solid rgba(0,0,0,.2);
    cursor: pointer;
    display: none;
  }
  